import React, { useState } from 'react';
import Icard from "./Icard.js";


function Card(pass) {
    let details = [
        {
            itemname: "fridge",
            price: 25,
            currency: "$",
            description: "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Amet numquam aspernatur!",
        },
        {
            itemname: "AC",
            price: 55,
            currency: "$",
            description: "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Amet numquam aspernatur! Lorem ipsum dolor sit amet.",
        },
        {
            itemname: "Tv",
            price: 45,
            currency: "$",
            description: "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Amet numquam aspernatur!",
        },
        {
            itemname: "Conditioner",
            price: 585,
            currency: "$",
            description: "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Amet numquam aspernatur!",
        },
        {
            itemname: "Computer",
            price: 15,
            currency: "$",
            description: "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Amet numquam aspernatur!",
        },
        {
            itemname: "Laptop",
            price: 46,
            currency: "$",
            description: "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Amet numquam aspernatur!",
        },

    ]
    const [curentname,setitemname] = useState([]);
    function btnclick(item){
        setitemname([...curentname,item])
    }
   
    function removecart(index){
        curentname.splice(index,1);
        setitemname([...curentname])
    }
    return (<div>
        <div class="container">

            <div class="row">
                <div class="col-lg-3">

                    <h1 class="my-4">Shop Name</h1>
                    <div class="list-group">
                        {
                           curentname.map((prod)=>{
                               return <li class="list-group-item d-flex justify-content-between align-items-center">
                               {prod.itemname} - {prod.currency}{prod.price}
                              <button class="btn btn-light" onClick={removecart} ><span class="badge badge-primary badge-pill ">X</span></button> 
                             </li>
                           })
                        }
                    </div>
                </div>


                <div class="col-lg-9">
                    <div class="row">
                        {
                            details.map((detail)=>{
                                return <Icard data={detail} click={btnclick}></Icard>
                            })
                        }

                    </div>
                </div>
            </div>
        </div>
    </div>);
}

export default Card;